/* AllDetails.css */

/* Style the view container */
.view-container {
    margin-top: 70px;
    padding: 20px;
  }
  
  /* Style the date cards */
  .date-card {
    background-color: transparent;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;
    color: #fff;
  }
  
  /* Style the table */
  .booking-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  /* Style table headers */
  .booking-table th {
    background-color: #f3f4f6;
    color: #100f0f;
    text-align: left;
    padding: 8px;
  }
  
  /* Style table rows */
  /* .booking-table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
   */
  /* Style table data cells */
  .booking-details td {
    padding: 8px;
    border-bottom: 1px solid #ddd;
  }
  