/* View.css */
.view-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .view-title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .table th, .table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .table th {
    background-color: #f2f2f2;
  }
  
  @media (max-width: 768px) {
    .table {
      font-size: 14px;
    }
  }
  